
.article{
  width: 100%;
  height: 580px;
  background-color: #f2f2f2;
  display: table;
  content: " ";
}
.article .center {
  width: 1020px;
  margin: 0 auto;
}
.article .center h3 {
  font-size: 28px;
  font-weight: inherit;
  color: #ff6600;
  margin: 42px auto;
}
.form-item{
  position: relative;
  border: solid 1px #d6dce0;
  width: 420px;
  height: 46px;
  z-index: 0;
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 2px;
}
.form-item .label{
  font-size: 16px;
  margin-left:15px;
  color: #313234;
}
.form-item span{
  position: absolute;
  top: 15px;
  right: -28px;
}
.form-item .code-no{
  right: -168px;
}
.form-item .regInput {
  border: 0 none;
  font-size: 16px;
  width: 190px;
  height: 16px;
  padding-bottom: 11px;
  padding-left: 12px;
  padding-top: 14px;
  outline: 0;
}
.btn-reg{
  height: 48px;
  border: none;
}
.btn-register {
  width: 100%;
  height: 48px;
  color: #fff;
  background: #ff6600;
  border: 0;
  font-size: 20px;
  outline: 0;
  border-radius: 2px;
}
.btn-input-w{
  width: 280px;
}
.form-item .btn-code-w{
  width: 150px;
}
.btn-phonecode {
  position: absolute;
  right: -140px;
  top: -1px;
  width: 133px;
  height: 48px;
  border: none;
  background: #ff6600;
  color: #fff;
  padding: 0;
  outline: 0;
  border-radius: 2px;
}
.reg-xieyi{
  position: relative;
  font-size: 14px;
  margin-bottom: 17px;
  color: #313234;
}
.reg-xieyi-c{
  color:#007eff;
}
button[disabled],button:disabled{
  background-color: rgba(255,179,128,1);
  color: #fff;
  border-radius: 2px;
}
.reg-left{
  float: left;
}
.reg-right{
  float: left;
  width: 500px;
  height: 480px;
  margin: 50px 0 0 90px;
  border-left: 1px solid #ccc;
}
.reg-right-text{
  width: 410px;
  height: 350px;
  background-color: #fff;
  border-radius: 2px;
  float: right;
  margin-top: 65px;
  text-align: center;
  color: #313234;
}
.reg-right-text ul{
  padding: 0;
}
.reg-right-text li{
  list-style-type: none;
  display: inline-block;
  margin: 6px;
  font-size: 14px;
}
.reg-right .login-icon{
  width: 45px;
  height: 45px;
}
.reg-right p{
  line-height: 12px;
}
.reg-right .reg-right-img-wb{
  background-position: 4px -62px;
}
.reg-right .reg-right-img-qq{
  background-position: 7px -110px;
}
.reg-right .reg-right-img-wx{
  background-position: 2px -161px;
}
.reg-right-text h4{
  color: #acb3b7;
  font-weight: normal;
}
.btn-register,.regInput,.btn-phonecode{
  font-family: "Microsoft YaHei","Hiragino Sans GB";
}



