
body{
  font-family: "Microsoft YaHei","Hiragino Sans GB";
	min-width: 1200px;
}
.nav-header{
  width: 1200px;
  height: 100px;
  margin: 0 auto;
}
.nav-header .logo {
  float: left;
  padding-top: 22px;
}
.nav-header .tip {
  margin-top: 66px;
  float: right;
  font-size: 14px;
  color: #313234;
}
.nav-header .tip .a {
  margin-left: 8px;
  color: #ff6600;
  text-decoration: none;
}
.color-ok{
	color: #5ce597;
}
.color-no{
	color: #e5dc5c
}
.login-icon{
  background: url(../assets/icon.png);
}
.el-checkbox__inner{
  width: 15px;
  height: 15px;
}
.el-checkbox__inner::after{
  height: 7px;
  left: 4px;
}
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}
@keyframes fadeInIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeInOut {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.fadeInIn {
  animation-name: fadeInIn;
}
.fadeInOut {
  animation-name: fadeInIn;
}



